import React from 'react';
import { NavLink } from 'react-router-dom';
import classNames from 'classnames';

const Nav = (props) => {
  let navClass = classNames({
    'nav': true,
    'nav--open': props.isOpen,
  });

  return (
    <nav className={ navClass }>
      <ul className="nav__list">
        <NavLink
          exact
          to="/"
          className="nav__link"
          onClick={ props.onClickMenuItem }
          activeClassName="nav__link nav__link--active">Accueil</NavLink>
        <NavLink
          exact
          to="/about"
          className="nav__link"
          onClick={ props.onClickMenuItem }
          activeClassName="nav__link nav__link--active"> propros</NavLink>
        <NavLink
          exact
          to="/skills"
          className="nav__link"
          onClick={ props.onClickMenuItem }
          activeClassName="nav__link nav__link--active">Comptences</NavLink>
        <NavLink
          exact
          to="/portfolio"
          className="nav__link"
          onClick={ props.onClickMenuItem }
          activeClassName="nav__link nav__link--active">Ralisations</NavLink>
        <NavLink
          exact
          to="/contact"
          className="nav__link"
          onClick={ props.onClickMenuItem }
          activeClassName="nav__link nav__link--active">Contact</NavLink>
      </ul>
    </nav>
  );
};

export default Nav;


